<template>
  <div>
    <top-back-nav :title="'게시판'"></top-back-nav>
    <content>
      <button
        @click="openModal"
        class="search-article__btn material-symbols-outlined">
        search
      </button>
      <button
        @click="$router.push({ name: 'boardnew' })"
        class="add-article__btn">
        +
      </button>
      <div class="cards" v-for="(board, idx) in boardData" :key="idx">
        <div
          class="cards__card"
          @click="$router.push('/article/board/' + board.articleNo)">
          <div class="card__author">
            작성자 ▪ {{ board.id }} | 작성일 ▪ {{ board.regTime.split(" ")[0] }}
          </div>
          <h2 class="card__title">{{ board.title }}</h2>
          <p class="card__content">
            {{ board.content }}
          </p>
          <div class="card__imgs">
            <img
              class="card__img"
              src="data:image/jpeg;base64," />
          </div>
        </div>
      </div>

      <!-- 모달창 -->
      <article-modal
        v-show="modal_show"
        @close="closeModal('')"></article-modal>
    </content>
    <app-footer></app-footer>
    <app-nav :navmode="'board'"></app-nav>
  </div>
</template>

<script>
import http from "@/util/http-common";
import AppNav from "@/components/layout/AppNav.vue";
import TopBackNav from "@/components/layout/TopBackNav.vue";
import ArticleModal from "@/components/common/ArticleModal.vue";
import AppFooter from "@/components/layout/AppFooter.vue";
export default {
  name: "BoardView",
  components: { AppNav, TopBackNav, ArticleModal, AppFooter },
  data() {
    return {
      message: "",
      modal_show: false,
      boardData: [],
    };
  },
  created() {
    http.get("/article/board/").then((response) => {
      this.boardData = response.data;
      console.log(response.data);
    });
  },
  methods: {
    kmpSearch(text, pattern) {
      const patternTable = this.buildPatternTable(pattern);
      let textIndex = 0;
      let patternIndex = 0;

      while (textIndex < text.length) {
        if (pattern[patternIndex] === text[textIndex]) {
          if (patternIndex === pattern.length - 1) {
            // Pattern found
            return textIndex - pattern.length + 1;
          }

          textIndex++;
          patternIndex++;
        } else if (patternIndex > 0) {
          patternIndex = patternTable[patternIndex - 1];
        } else {
          textIndex++;
        }
      }
      return -1;
    },
    buildPatternTable(pattern) {
      const table = [0];
      let prefix = 0;
      let suffix = 1;

      while (suffix < pattern.length) {
        if (pattern[prefix] === pattern[suffix]) {
          table[suffix] = prefix + 1;
          prefix++;
          suffix++;
        } else if (prefix === 0) {
          table[suffix] = 0;
          suffix++;
        } else {
          prefix = table[prefix - 1];
        }
      }
      return table;
    },
    openModal() {
      this.modal_show = true;
    },
    closeModal(search_keyword) {
      this.modal_show = false;
      //전체 목록 불러오기
      if (search_keyword != "") {
        http.get("/article/board/").then((response) => {
          this.boardData = response.data;

          //KMP 알고리즘
          let test = {};
          let idx = 0;
          for (let info of this.boardData) {
            if (this.kmpSearch(info.title, search_keyword) != -1) {
              console.log(info);
              test[idx++] = info;
            }
          }
          this.boardData = test;
        });
      }
    },
  },
};
</script>

<style scoped>
content {
  padding: 0 1rem;
}
.add-article__btn {
  position: fixed;
  right: 1rem;
  bottom: 5rem;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 2rem;
  font-weight: 200;
  color: white;
  border-radius: 50%;
  background-color: #3485ff;
  border: solid 0;
  box-shadow: 2px 2px 2px 1px rgba(128, 128, 128, 0.29);
}

.search-article__btn {
  position: fixed;
  right: 1rem;
  bottom: 8rem;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.4rem;
  color: white;
  border-radius: 50%;
  background-color: #8d68f3;
  border: solid 0;
  box-shadow: 2px 2px 2px 1px rgba(128, 128, 128, 0.29);
}

.cards__card {
  padding: 1rem;
  margin: 0 0.5rem 1rem 0.5rem;
  border-radius: 5px;
  background-color: white;
  box-shadow: 2px 2px 2px 1px rgba(128, 128, 128, 0.29);
}
.card__author {
  font-size: 0.7rem;
  text-align: start;
  margin: 1rem;
}
.card__title {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 1rem;
  text-align: start;
}
.card__content {
  font-size: 0.9rem;
  text-align: start;
  padding: 0 1rem;
  margin: 1rem 0;
  width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  white-space: normal;
  overflow: hidden;
}
.card__img {
  width: 90%;
}
</style>
